.gallery {
  z-index: 8;
}

.background,
.content {
  position: fixed;
}

.background {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 8;
}

.content {
  top: 50%;
  right: 50%;
  z-index: 9;
  transform: translate(50%, -50%);
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}

.imageContainer img {
  width: 95vw;
  height: auto;
}

.closeButton {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
}

.closeButton > button {
  border-radius: 0.5rem;
}

.nextButton {
  position: absolute;
  z-index: 5;
  right: 1rem;
}

.prevButton {
  position: absolute;
  z-index: 5;
  left: 1rem;
}

.prevButton > button,
.nextButton > button {
  background-color: rgba(0, 0, 0, 0.3);
}

.loadedImage {
  display: block;
}

.unloadedImage {
  display: none;
}

@media only screen and (device-width: 768px) {
  .imageContainer {
    min-width: 80vw;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}

@media only screen and (min-width: 769px) {
  .imageContainer {
    min-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .imageContainer img {
    width: auto;
    height: 80vh;
  }
  .closeButton {
    top: 0;
    right: 2rem;
  }
  .nextButton,
  .prevButton {
    position: relative;
  }
}

/* Portrait */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 1.5) {
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}

/* Landscape */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 1.5) {
  .imageContainer img {
    width: 80vw;
    height: auto;
  }
}
